import React from 'react'
import { categoriesData, metaTagsData } from '../../data'
import { MetaTags } from "../index"
import { useTranslation } from "react-i18next"
import loadable from '@loadable/component'

const MainPage = () => {
    const { t } = useTranslation()
    const MetaTags = loadable(() => import('../PartialElements/MetaTags'))
    const Slider = loadable(() => import('./Slider'))
    const GridView = loadable(() => import('../VisualElements/GridView'))
    return <>
        <MetaTags {...metaTagsData.mainPage}/>
        <Slider />
        <h3 style={{ "textAlign": "center", margin: 30 }}>{t(" ")}</h3>
        <GridView cardItems={[
            ...Object.values(categoriesData.categories),
            ...Object.values(categoriesData.uncategorizedSubcategories),
        ]} />
    </>
}

export default MainPage
